.collections {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: auto;
    gap: 30px;
    @include media($md) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include media($xs) {
        grid-template-columns: 1fr;
    }

    .collection {
        display: flex;
        position: relative;
        background: $coll;
        border-radius: 6px;
        @include media ($xs) {
            img {
                max-width: 100%;
                height: 200px;
                object-fit: cover;
            }
        }

        &:nth-child(3) {
            @include media($md) {
                grid-column: span 2;

                img {
                    max-width: 100%;
                    height: 200px;
                    object-fit: cover;
                }
            } 
            @include media($xs) {
                grid-column: 1;
            }
                
        }

        img {
            position: absolute;
            z-index: -5;
            max-width: 362px;
            width: 100%;
            min-height: 200px;
            border-radius: 6px;
        }

        &__link {
            display: inline-block;
            font-size: 49px;
        }

        &__title {
            font-size: 20px;
            color: $white;
            line-height: 22px;
            font-family: "Comfortaa";
            font-weight: 400;
            margin-top: 16px;
        }

        .collect {
            text-align: center;
            width: 100%;
            padding-top: 59px;
            min-height: 200px;
            .boxes1 {
                position: absolute;
                left: 0;
                top: 0;
            }
            .boxes2 {
                position: absolute;
                right: 0;
                top: 0;
            }
            .box {
                width: 20px;
                height: 25px;
                background: $main;
                margin: 14px 10px;
            }
        }
    }
}