footer {
    padding-top: 25px;
    padding-bottom: 29px;
    background: $footer;

    .footer {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__apps {
            display: flex;
        }

        &__link {
            position: relative;
        }
        
        &__img {
            height: 25px;
            position: absolute;
        }

        &__text {
            display: inline-block;
            max-width: 130px;
            color: $white;
            width: 100%;
            font-size: 9px;
            padding-left: 30px;

            span {
                font-size: 16px;
            }
        }
        &__android {
            margin-right: 50px;
            @include media($sm) {
                margin-right: 15px;
            }
        }
        &__ios {
            margin-right: 15px;
        }

        &__info {
            font-size: 18px;
            font-family: "Comfortaa";
            line-height: 20px;
            font-weight: 700;
            color: $white;
        }
    }
}