.premiers {
    position: relative;
    background: $pr;
    min-height: 500px;

    &__bg {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -10;
        height: 100%;
    }

    &__img {
        max-width: 100%;
        object-fit: cover;
    }

    &__info {
        z-index: 10;
        padding-top: 43px;
        padding-bottom: 80px;
        @include media($xs) {
            text-align: center;
        }

        p {
            font-size: 18px;
            line-height: 27px;
            font-family: "Roboto";
            font-weight: 400;
            color: $black;
            max-width: 558px;
            margin-top: 50px;
            margin-bottom: 55px;
        }
    }
}