.series {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(190px, auto);
    gap: 30px;
    padding-bottom: 60px;

    @include media($sm) {
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: minmax(150px, auto);
    }
    @include media($xs) {
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: minmax(130px, auto);
        gap: 15px;
    }

    &__block {
        position: relative;
        text-align: center;
        border-radius: 6px;
    }
    
    &__desc {
        position: absolute;
        top: 0;
        left: 0;
        padding: 15px;
        background: $ser;
        width: 100%;
        min-height: 190px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        z-index: 10;
        border-radius: 6px;
        @include media($sm) {
            min-height: 150px;
        }
        @include media($xs) {
            min-height: 130px;
        }

        a {
            margin-right: 0;
        }
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 6px;
    }
    &__block:nth-child(1) {
        grid-column: span 4;
        grid-row: span 2;
        @include media($sm) {
            grid-column: span 3;
            grid-row: span 2; 
        }
        @include media($xs) {
            grid-column: span 2;
            grid-row: span 2; 
        }
    }
    &__block:nth-child(2) {
        grid-column: span 2;
        grid-row: span 2;
        @include media($xs) {
            grid-column: 1fr;
            grid-row: span 2;
        }
    }

    &__block:nth-child(6) {
        @include media($sm) {
            grid-column: span 2;
        }
        @include media($xs) {
            grid-column: 2;
        }
    }

    &__link {
        font-size: 48px;
        display: inline-block;
        padding-bottom: 30px;
        @include media($xs) {
            font-size: 25px;
            padding-bottom: 15px;
        }
    }

    &__title {
        font-size: 24px;
        line-height: 27px;
        color: $white;
        font-family: "Comfortaa";
        font-weight: 700;
        @include media($xs) {
            font-size: 18px;
        }
    }

    &__title-lg {
        font-size: 40px;
        line-height: 44px;
        color: $white;
        font-family: "Comfortaa";
        font-weight: 700;
        margin-bottom: 30px;
        @include media($xs) {
            font-size: 28px;
        }
    }

    &__text {
        font-family: "Comfortaa";
        font-size: 16px;
        font-weight: 400;
        color: $white;
        line-height: 24px;
        margin-bottom: 30px;
        @include media($sm) {
            @include limit(3)
        }
        @include media($xs) {
            @include limit(2)
        }
    }
}